  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 15px #2d3986 solid;
  overflow-y: scroll; }
  .about__h1 {
    font-size: 3rem;
    text-transform: uppercase;
    padding: 10px 40px; }
  .about__button {
    position: relative;
    z-index: 4;
    margin: 5% 0;
    color: #2d3986;
    background-color: transparent;
    border: 1px solid #2d3986;
    padding: 10px 15px;
    text-transform: uppercase;
    cursor: pointer; }
    .about__button::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 120%;
      height: 100%;
      background-color: #202960;
      transform: translate(-50%, -50%) rotate(-5deg);
      opacity: 0;
      transition: 0.3s ease-in-out;
      z-index: -1; }
    .about__button:hover::before {
      opacity: .7; }

@media screen and (max-width: 760px) {
  .about {
    height: 100vh;
    width: 100vw;
    z-index: 1;
    border: none; } }
